import React, { FC } from "react";

import { StyledScreenBackground, StyledScreenBackgroundProps } from "./ScreenBackground.styles";

export interface ScreenBackgroundProps extends StyledScreenBackgroundProps {
	dataQA: string;
}

const ScreenBackground: FC<ScreenBackgroundProps> = ({ dataQA, backgroundImageURL, backgroundColor }) => (
	<StyledScreenBackground data-qa={dataQA} backgroundImageURL={backgroundImageURL} backgroundColor={backgroundColor} />
);

ScreenBackground.displayName = "ScreenBackground";
export default ScreenBackground;
